import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router';
import { getInfosUser, getUserActivity, getUserAverage } from '../../api/services';
import Sidebar from '../../components/SideBar';
import BarChart from '../../components/BarChart';
import LineChart from '../../components/Linechart';
import RadarChart from '../../components/Radarchart';
import RadialChart from '../../components/Radialchart';
import { getUserPerformanceMocked, getInfosUserMocked, getUserActivityMocked, getUserAverageMocked } from '../../api/mocks/services';

function Dashboard() {

    const [userInformation, setUserInformation] = useState(null);
    const [userActivity, setUserActivity] = useState([]);
    const [userPerformance, setUserPerformance] = useState([]);
    const [isError, setIsError] = useState(false);
    const [isDataLoading, setDataLoading] = useState(false);
    const { id } = useParams();
    const [userAverage, setUserAverage] = useState([]);


    useEffect(() => {

        const develop = true;

        if (develop) {  // for working with local file 
            setUserInformation(getInfosUserMocked(id));
            setUserActivity(getUserActivityMocked(id));
            setUserAverage(getUserAverageMocked(id));
            setUserPerformance(getUserPerformanceMocked(id));
           
          //  setTimeout(function(){
                setDataLoading(true);
           // },900);
            
        } else {     //for working with api on serveur
            Promise.all([
                getInfosUser(id).then((dataInfo) => {
                    setUserInformation(dataInfo);
                }),
                getUserActivity(id).then((dataActivity) => {
                    setUserActivity(dataActivity); //.then(({ results }) => this.setState({ customer: results }))
                }),
                getUserAverage(id).then((dataAverage) => {
                    setUserAverage(dataAverage);
                })
               // getuser
            ]).then((resp) => {
                setDataLoading(true)

            }).catch((err) => {
                setIsError(true)
                console.log("Serveur en maintenance, Merci de ressayer ulterieurement :"+err)
            })

        }
    }, [id]);

    /* console.log("userInformation"+userInformation.data);
      console.log("userActivity"+userActivity.data.userId); */

    return (
        <div className="container-profil">
            <div className="container-profil__sidebar">
                <Sidebar />
            </div>
            {isDataLoading &&(
                <section className="container-profil__dashboard">
                    <div className="profil__dashboard-header">
                        <h1 className="profil__dashboard-title sr-only">résumé des performances</h1>
                        <h2 className="profil__dashboard-user" key={`${userInformation.data.id}`}>{`Bonjour ${userInformation.data.userInfos.firstName}`}</h2>
                        <p className="profil__dashboard-resume">Félicitation ! vous avez explosé vos objectifs hier</p>
                    </div>
                    <div className="profil__dashboard-activities">
                        <div className="profil__activities-center">
                            <div className="profil__activity-today">
                                <BarChart propsData={userActivity} />
                            </div>

                            <div className="profil__activity-coverage">
                                <LineChart propsDataAverage={userAverage} />
                            </div>

                            <div className="profil__activity-radar">
                                <RadarChart propsDataPerformance={userPerformance} />
                            </div>
                            
                            <div className="profil__activity-score">
                                <RadialChart propsDataRadial={userInformation} />
                            </div>
                        </div>
                        <div className="profil__activity-side">
                            <p> Calories</p>
                            <p>Proteines</p>
                            <p>Glucides</p>
                            <p>Lipides</p>
                        </div>
                    </div>
                </section>
             ) }
             {isError &&(
                    <h1 className="error">Une erreure est survenue, merci de reessayer</h1>
            )} 
           

        </div>
    );
}

export default Dashboard

